@charset "UTF-8";
body {
  font-family: "Zen Kaku Gothic New", sans-serif;
}

body {
  color: #4D2200;
}

@media screen and (max-width: 768px) {
  .u-desktop {
    display: none;
  }
}

.u-mobile {
  display: none;
}
@media screen and (max-width: 768px) {
  .u-mobile {
    display: block;
  }
}

.js-fade {
  opacity: 0;
  transform: translateY(10%);
  transition: all 1s cubic-bezier(0.155, 0.91, 0.695, 0.95);
}

.js-fade.is-active {
  opacity: 1;
  transform: translateY(0);
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 1200px) {
  html {
    font-size: 1.3333333333vw;
  }
}
@media screen and (max-width: 768px) {
  html {
    font-size: 16px;
  }
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}

/* pcの電話番号発信対応 */
@media screen and (max-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (max-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
  height: auto;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.c-blur {
  filter: blur(0.625rem);
  transition: all 2s;
}

.c-blur.is-active {
  filter: blur(0);
}

.c-bnr {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 3.125rem;
}
@media screen and (max-width: 768px) {
  .c-bnr {
    grid-template-columns: 1fr;
    gap: 1.125rem;
  }
}
.c-bnr a {
  display: block;
  position: relative;
}
.c-bnr a::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  background-color: #000;
  width: 100%;
  height: 100%;
  pointer-events: none;
  transition: all 0.3s;
  opacity: 0;
}
.c-bnr a:hover::before {
  opacity: 0.2;
}

.c-btn {
  display: inline-flex;
  align-items: center;
  position: relative;
  background-color: #fff;
  justify-content: center;
  font-size: 0.9375rem;
  font-weight: 500;
  letter-spacing: 0.1em;
  border-radius: 0.625rem;
  transition: all 0.3s;
  width: 21.125rem;
  height: 3.125rem;
}

.c-btn::before {
  position: absolute;
  content: "";
  width: 1.5rem;
  height: 0.375rem;
  top: 50%;
  transform: translateY(-50%);
  right: 0.75rem;
  mask: url(../images/common/arrow.svg) center center/contain no-repeat;
  background-color: #4D2200;
  transition: all 0.3s;
}

.c-btn:hover {
  background-color: #4D2200;
  color: #fff;
}
.c-btn:hover::before {
  background-color: #fff;
}

