//pc
html {
  font-size: 16px;

  //~375px
  @media (max-width: 1200px) {
    font-size: vw(1200, 16);
  }

  @include mq(md) {
    font-size: 16px;
  }

  @media (max-width: 375px) {
    font-size: vw(375, 16);
  }

}

/* pcの電話番号発信対応 */
a[href^="tel:"] {
  @include mq("md") {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
  @include mq("md") {
    &:hover {
      opacity: 0.8;
    }
  }
}
